import Vue from "vue";
import Router from "vue-router";
Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      component: () => import("@/view/user/detailed.vue"),
      meta: {
        title: '收益明细'
      }
    },
    {
      path: "/GoldCoin",
      component: () => import("@/view/user/GoldCoin.vue"),
      meta: {
        title: '星币明细'
      }
    },
    {
      path: "/market",
      component: () => import("@/view/market"),
      meta: {
        title: '市场'
      }
    },
    {
      path: "/earnestMoney",
      component: () => import("@/view/earnestMoney"),
      meta: {
        title: '保证金'
      }
    },
    {
      path: "/browser",
      component: () => import("@/view/browser"),
      meta: {
        title: 'Dapp'
      }
    },
    {
      path: "/chooseIdentity",
      component: () => import("@/view/chooseIdentity"),
      meta: {
        title: '身份选择'
      }
    },
    {
      path: "/information",
      component: () => import("@/view/information"),
      meta: {
        title: '基本信息'
      }
    },
    {
      path: "/myNeeds",
      component: () => import("@/view/myNeeds"),
      meta: {
        title: '我的需求'
      }
    },
    {
      path: "/myService",
      component: () => import("@/view/myService"),
      meta: {
        title: '我的服务'
      }
    },
    {
      path: "/demandDetails",
      component: () => import("@/view/demandDetails"),
      meta: {
        title: '需求详情'
      }
    },
    {
      path: "/improveInformation",
      component: () => import("@/view/improveInformation"),
      meta: {
        title: '完善参与信息'
      }
    }
  ],
  mode: 'hash',
  base: process.env.VUE_APP_BUILD,
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return {
        x: 0,
        y: 0
      }
    }
  }
});
