export function taskStatusTips (CurrentIdentity, my_status, status) {
  if (CurrentIdentity === 'serve') {
    switch (my_status && status === 0) {
      case -1:
        return '待参与'
        break;
      case 0:
        return '已参与'
        break;
      case 1:
        return '参与中'
        break;
      case 2:
        return '已完成'
        break;
      case 3:
        return '已结束'
        break;
      default:
        return seatus1(status)
        break;
    }
  } else {
    return seatus1(status)
  }
  function seatus1 (status) {
    switch (status) {
      case 0:
        return '匹配中'
        break;
      case 1:
        return '服务中'
        break;
      case 2:
        return '已完成'
        break;
      case 3:
        return '暂停需求'
        break;
      case 4:
        return '关闭需求'
        break;
    }
  }
}
