<template>
  <van-nav-bar ref="navBar" :title="$route.meta.title" left-arrow @click-left="clickLeft(save)"
    :class="bgColor ? 'bgColor' : ''" fixed placeholder safe-area-inset-top />
</template>
<script>
export default {
  props: {
    save: {
      type: String, // 返回事件
      default: ''
    },
    bgColor: {
      type: String, // 背景颜色
      default: '#1F75D9'
    }
  },
  watch: {
    bgColor (val) {
      console.log(val)
      this.$refs.navBar.$el.style.setProperty('--bgBackground', val)
    }
  },
  mounted () { },
  methods: {
    clickLeft (val) {
      if (val) {
        this.$emit('previousStep')
      } else {
        this.$router.go(-1)
      }
    }
  }
}
</script>
<style scoped lang="less">
.bgColor {
  --bgBackground: #1F75D9;
  border: none;

  /deep/.van-nav-bar {
    background-color: var(--bgBackground);

    .van-icon-arrow-left.van-nav-bar__arrow {
      color: #fff;
    }

    .van-nav-bar__title {
      color: #fff;
    }
  }

  /deep/.van-nav-bar::after {
    border: none;
  }
}


/deep/.van-icon-arrow-left.van-nav-bar__arrow {
  color: #3F3F3F;
}
</style>